#footer {
    display: flex;
    width : 100%;
    height : 2.5rem;
    justify-content: center;
    background-color: black;
    color: white;
}

#button {
    display: flex;
    width: 130px;
    height: 2.5rem;
    position: absolute;
    right: 7px;
    background-color: blue;
    color: white;
    font-size: 15px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}
#button:hover {
    background-color: red;
    cursor: pointer;
    font-size: 20px;
}

#footer p {
    display: flex;
    align-items: center;
    justify-content: center;
}
